import $ from "jquery";
import html2canvas from "html2canvas";
import BugBattle from "./BugBattle";
export var startScreenCapture = function startScreenCapture(mainColor, snapshotPosition) {
  return checkOnlineStatus(window.location.origin).then(function (status) {
    if (status && status.up) {
      return prepareRemoteScreenshot(snapshotPosition);
    } else {
      return prepareHTML2canvas(mainColor, snapshotPosition);
    }
  });
};

var checkOnlineStatus = function checkOnlineStatus(url) {
  return new Promise(function (resolve, reject) {
    var xhr = new XMLHttpRequest();

    xhr.onreadystatechange = function () {
      if (xhr.readyState === XMLHttpRequest.DONE) {
        var status = JSON.parse(xhr.responseText);
        resolve(status);
      }
    };

    xhr.ontimeout = function () {
      reject();
    };

    xhr.onerror = function () {
      reject();
    };

    xhr.open("GET", "https://uptime.bugbattle.io/?url=" + encodeURIComponent(url), true);
    xhr.send(null);
  });
};

var prepareHTML2canvas = function prepareHTML2canvas(mainColor, snapshotPosition) {
  return html2canvas(document.body, {
    x: snapshotPosition.x,
    y: snapshotPosition.y,
    width: window.innerWidth,
    height: window.innerHeight,
    letterRendering: 1,
    allowTaint: true,
    useCORS: false,
    logging: false,
    scale: window.devicePixelRatio,
    imageTimeout: 10000,
    proxy: "https://jsproxy.bugbattle.io/"
  }).then(function (canvas) {
    if (canvas) {
      return prepareScreenshot(canvas.toDataURL(), mainColor);
    } else {
      return Promise.reject();
    }
  });
};

var prepareScreenshot = function prepareScreenshot(screenshot, mainColor) {
  return new Promise(function (resolve, reject) {
    var imageObj = new Image();

    imageObj.onload = function () {
      var pixelRatio = window.devicePixelRatio;
      var canvas = document.createElement("canvas");
      canvas.width = this.width;
      canvas.height = this.height;
      var context = canvas.getContext("2d"); // Draw image

      context.drawImage(imageObj, 0, 0, this.width, this.height); // Draw markers

      var editorDot = document.querySelector(".bugbattle-screenshot-editor-dot");
      var editorRectangle = document.querySelector(".bugbattle-screenshot-editor-rectangle");

      if (editorDot && editorRectangle) {
        context.beginPath();
        context.arc((editorDot.offsetLeft + editorDot.offsetWidth / 2) * pixelRatio, (editorDot.offsetTop + editorDot.offsetHeight / 2) * pixelRatio, 6 * pixelRatio, 0, 2 * Math.PI, false);
        context.fillStyle = mainColor;
        context.fill();
        context.closePath();
        context.lineWidth = 3 * pixelRatio;
        context.strokeStyle = mainColor;
        context.stroke();

        if (editorRectangle.offsetTop > 0 || editorRectangle.offsetLeft) {
          context.strokeRect(editorRectangle.offsetLeft * pixelRatio, editorRectangle.offsetTop * pixelRatio, editorRectangle.offsetWidth * pixelRatio, editorRectangle.offsetHeight * pixelRatio);
        }
      }

      uploadScreenshot(canvas.toDataURL("image/jpeg", 0.5)).then(function (fileUrl) {
        resolve({
          fileUrl: fileUrl
        });
      }).catch(function (err) {
        reject(err);
      });
    };

    imageObj.onerror = function () {
      reject();
    };

    imageObj.src = screenshot;
  });
};

var uploadScreenshot = function uploadScreenshot(screenshot) {
  return new Promise(function (resolve, reject) {
    var http = new XMLHttpRequest();
    http.open("POST", BugBattle.instance.apiUrl + "/uploads/sdk");
    http.setRequestHeader("Api-Token", BugBattle.instance.sdkKey);

    http.onreadystatechange = function (e) {
      if (http.readyState === XMLHttpRequest.DONE) {
        try {
          var response = JSON.parse(http.responseText);
          console.log(response);

          if (response && response.fileUrl) {
            resolve(response.fileUrl);
          } else {
            reject();
          }
        } catch (e) {
          reject(e);
        }
      }
    };

    var file = dataURItoBlob(screenshot);
    var formData = new FormData();
    formData.append("file", file, "screenshot.jpg");
    http.send(formData);
  });
};

var dataURItoBlob = function dataURItoBlob(dataURI) {
  var byteString = atob(dataURI.split(",")[1]);
  var mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0];
  var ab = new ArrayBuffer(byteString.length);
  var ia = new Uint8Array(ab);

  for (var i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }

  var blob = new Blob([ab], {
    type: mimeString
  });
  return blob;
};

var prepareRemoteScreenshot = function prepareRemoteScreenshot(snapshotPosition) {
  return new Promise(function (resolve, reject) {
    $(document).find("iframe, video, embed").each(function () {
      var width = $(this).width();
      var height = $(this).height();
      $(this).attr("bugbattle-element", true);
      $(this).attr("bugbattle-width", width);
      $(this).attr("bugbattle-height", height);
    });
    var clone = $(document.documentElement).clone(true, true); // Cleanup

    $(document).find("[bugbattle-element=true]").each(function () {
      $(this).attr("bugbattle-element", null);
      $(this).attr("bugbattle-width", null);
      $(this).attr("bugbattle-height", null);
    });
    clone.find("script, noscript").remove(); // Cleanup base path

    clone.remove("base");
    clone.find("head").append('<base href="' + window.location.origin + '">');
    clone.find(".bugbattle--feedback-dialog-container").remove();
    clone.find(".bugbattle-screenshot-editor-borderlayer").remove();
    clone.find("[bugbattle-element=true]").each(function () {
      $(this).css("width", $(this).attr("bugbattle-width"));
      $(this).css("height", $(this).attr("bugbattle-height"));
    });
    var node = document.doctype;
    var html = "<!DOCTYPE " + node.name + (node.publicId ? ' PUBLIC "' + node.publicId + '"' : "") + (!node.publicId && node.systemId ? " SYSTEM" : "") + (node.systemId ? ' "' + node.systemId + '"' : "") + ">";
    html += clone.prop("outerHTML");
    resolve({
      html: html,
      baseUrl: window.location.origin,
      x: snapshotPosition.x,
      y: snapshotPosition.y,
      width: window.innerWidth,
      height: window.innerHeight
    });
  });
};